body {
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    background-color: #D5E5E6;
    font-family: 'Inter', sans-serif;
    font-size: 16px;
}

@media (max-width: 500px) {
    body {
        font-size: 8px;
    }
}

h2 {
    font-size: 1.2em;
    font-weight: 600;
}

h3 {
    font-size: 1em;
    font-weight: 400; 
}

a, span, p {
    color: #1F1F1F;
}